.graphcard{
    height: 370px;
    display: flex;
    flex-direction: row;
    flex: 1;
    flex-shrink: 0;
    flex-grow: 0;
    align-items: center;
}

.label{
    padding: 0;
    display: inline;
}
.info{
    display: flex;
    width: 85;
    margin: 0;
    margin-block-start: 0;
    margin-block-end: 0;
    padding-left: 10px;
    padding-right: .5em;
}
.name{
    display: block;
    margin-block-start: 0;
    margin-block-end: 0;
    padding-right: .5em;
    width : 100px;
}

.info:hover{
    background-color: rgb(236, 236, 236);
}

.pap{
    margin: 1em;
    padding-right: 1em;
}